@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.header{
    display: flex;
    justify-content: center;
    margin-bottom: 1.2rem;
    align-items: center;
}

.kasa-logo{
   width: 100px;
}

.kasa-nav{
    flex-grow: 2;
    display: flex;
}
.kasa-nav-list__link{
    list-style-type: none;
    width:100%;
    display: flex;
    justify-content: flex-end;
    color: #ff6060;
    
 }
 .kasa-mg-r1{
     margin-right:1rem;
 }

 .kasa-red{
    color: #ff6060;
 }

.nav-link{
     text-decoration: none;
 }

 .nav-link.current{
     text-decoration: underline;
     color: #ff6060
 }

 @media screen and (min-width: 780px){
    .kasa-logo{
        width:145px;
    }
}
.kasa-footer-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height : 209px;
    background-color: black;
    margin: 2rem -1.2rem 0;
}
.kasa-footer-logo path {
    fill:  white;
}
.kasa-footer-logo{
    height : 39.44px;
    width: 122px;
}

.kasa-footer-container p{
    margin-top: 1rem;
    font-size: 12px;
    font-weight: 500;
    color: white;
}

@media screen and (min-width: 780px){
    .kasa-footer-container{
        margin: 2rem -4rem 0;
    }
  
  }

  @media screen and (min-width: 780px){
    .kasa-footer-container{
        margin: 2rem -6.25rem 0;
    }
  }
.kasa-banner-container{
    display: flex;
    border-radius: 10px;
    background-color: black;
    mix-blend-mode: darken;
    align-items: center;
    justify-content: left;
    /* height: 111px; */
    /* margin: 0 100px 0 100px; */
}

.kasa-banner-accueil-size{
    height: 111px;
}

.kasa-banner-about-size{
    height: 223px;
}

.kasa-banner__img{
    width: 100%;
    height: 100%;
    opacity: 0.8;
    object-fit: cover;
    border-radius: 10px;
}

.kasa-banner__txt{
    position:absolute;
    color : white;
    font-size: 1.5rem;
    width: 217px;
    margin-left: 1rem;
}

@media screen and (min-width: 780px){
    .kasa-banner-container{
        justify-content: center;
    }
    .kasa-banner__txt{
        font-size: 2rem;
        width: auto;
        margin-left: 0;
    }
}

@media screen and (min-width: 1000px){
    .kasa-banner-accueil-size{
        height: 223px;
    }
    .kasa-banner__txt{
        font-size: 3rem;
    }

}



