.kasa-card{
    display: flex;
    width:100%;
    background-color : #ff6060;
    border-radius: 10px;
}


.kasa-card__img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}

.kasa-card-link{
    position: relative;
    text-decoration: none;
    text-decoration: none;
    
}

.kasa-card__txt{
    position: absolute;
    top: 170px;
    margin-left: 1rem;
    width: 295px;
    font-size: 1.1rem;
    color: white;
}
.kasa-card-container{
    list-style-type: none;
    /* background-color: #f7f7f7;
    border-radius: 25px;*/
    padding: 0;
    margin-top: 2rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    /*flex-wrap: wrap;*/
}

.kasa-card-list:not(:nth-child(1)){
    margin-top: 2rem;
}

@media screen and (min-width: 780px){

    .kasa-card-container{
        background-color: #f7f7f7;
        border-radius: 25px;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 50px 50px;
    }

    .kasa-card-list:not(:nth-child(1)){
        margin-top: 0;
    }

    .kasa-card-list{
        margin-bottom: 2rem;
        flex-basis: 45%;
    }
}

@media screen and (min-width: 1000px){
    .kasa-card-list{
        flex-basis: 30%;
    }

}

.dropdown-container{
    display :flex;
    flex-direction: column;
    width:100%;
    border-radius: 5px;
    justify-content: space-between;
    margin-top: 1rem;
}
.dropdown__title{
    display: flex;
    justify-content:space-between;
    background-color: #FF6060;
    border-radius: 5px;
    height : 30px;
    align-items: center;
    align-content: center;
    padding-left: 1rem;
    padding-right: 1rem;
}

.dropdown__list{
    list-style-type: none;
    height: 100%;

}

.dropdown__title > h2{
    color:white;
    font-size:13px;
    font-weight: 500;
}

.dropdown__txt{
    background-color: #f7f7f7;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    width: 100%;
    padding: 20px;
    color: #FF6060;
}

.arrow_down {
    transform: rotate(180deg);
    transform-origin: center;
    height : 1rem;
    width : 1rem;
}

.arrow_default{
    height : 1rem;
    width : 1rem;
}

.hidden{
    display: none;
}

@media screen and (min-width: 780px){
    .dropdown-container{
        flex-basis: 45%;
        height: 100%;
    }
}

@media screen and (min-width: 780px){
    .kasa-about-dropdown-container{
     width: 80%;
     margin: auto;
    }
}

